import Index from '@/pages/Index'
import BattlePass from '@/pages/BattlePass'
import Bonuses from '@/pages/Bonuses'
import Top from '@/pages/Top'
import Referrals from '@/pages/Referals'
import ProvablyFair from '@/pages/ProvablyFair/Index'
import DistributeProvablyFair from '@/pages/ProvablyFair/distribute'
import Support from '@/pages/Support/Index'
import DistributeSupport from '@/pages/Support/distribute'
import Crash from '@/pages/Crash'
import Double from '@/pages/Double'
import Wheel from '@/pages/Wheel'
import Jackpot from '@/pages/Jackpot'
import Profile from '@/pages/account/Profile'
import GamesAccount from '@/pages/account/Games'
import DepositsAccount from '@/pages/account/Deposits'
import WithdrawalsAccount from '@/pages/account/Withdrawals'
import PaymentDistribute from '@/pages/payment/distribute'
import { createRouter, createWebHistory } from 'vue-router'

const routes = [
    {
        path: '/',
        component: Index
    },
    {
        path: '/season-pass',
        component: BattlePass
    },
    {
        path: '/bonuses',
        component: Bonuses
    },
    {
        path: '/top',
        component: Top
    },
    {
        path: '/referrals',
        component: Referrals
    },
    {
        path: '/provably-fair',
        component: ProvablyFair
    },
    {
        path: '/provably-fair/:article',
        component: DistributeProvablyFair
    },
    {
        path: '/support',
        component: Support
    },
    {
        path: '/support/:article',
        component: DistributeSupport
    },
    {
        path: '/crash',
        component: Crash
    },
    {
        path: '/double',
        component: Double
    },
    {
        path: '/wheel',
        component: Wheel
    },
    {
        path: '/jackpot',
        component: Jackpot
    },
    {
        path: '/profile',
        component: Profile
    },
    {
        path: '/profile/games',
        component: GamesAccount
    },
    {
        path: '/profile/deposits',
        component: DepositsAccount
    },
    {
        path: '/profile/withdrawals',
        component: WithdrawalsAccount
    },
    {
        path: '/payment/:article',
        component: PaymentDistribute
    },
    {
        path: '/payment',
        redirect: '/payment/money'
    }
]

const router = createRouter({
    routes,
    history: createWebHistory(process.env.BASE_URL),
})

export default router;
