<style>
:root {
  --gutter: 2rem;
  --gutter-page: 4rem;
  --gutter-shop: 2.5rem;
  --gutter-support: 2.5rem;
}
@media all and (max-width: 991px) {
  :root {
    --gutter: 1.5rem;
    --gutter-page: 2rem;
  }
}
.profile-wrapper {
  display: flex;
  flex-direction: column;
  width: 121rem;
  margin: 0 auto;
  font-family: Gotham;
}
.profile-wrapper .empty-block_not-logged {
  min-height: 50vh;
}
.profile-wrapper .profile {
  display: block;
  width: 100%;
  font-family: Gotham;
}
.profile-wrapper .profile .row {
  display: flex;
  margin-bottom: 2.6rem;
  flex-direction: column;
}
.profile-wrapper .profile .row__content {
  display: flex;
  gap: 1.5rem;
}
.profile-wrapper .profile .row__title {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  color: #cddbff;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 1.9rem;
  margin-bottom: 1.5rem;
}
.profile-wrapper .profile .row__title .icon {
  font-size: 2.8rem;
}
.profile-wrapper .profile .profile__statistics {
  display: flex;
  gap: 0.8rem;
  width: 100%;
}
.profile-wrapper .profile .profile__widgets {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
}
.profile-wrapper .profile .profile__actions {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(24rem, 1fr));
  gap: 0.7rem;
}
@media all and (max-width: 991px) {
  .profile-wrapper .profile .row {
    margin-bottom: 1rem;
  }
  .profile-wrapper .profile .row__title {
    margin-bottom: 1rem;
    font-size: 1.4rem;
    line-height: 1.7rem;
  }
  .profile-wrapper .profile .row__title .icon {
    font-size: 2.2rem;
  }
  .profile-wrapper .profile .row_block {
    margin-top: 1.5rem;
  }
  .profile-wrapper .profile .row__content:not(.profile__actions) {
    flex-direction: column;
    gap: 1rem;
  }
  .profile-wrapper .profile .profile__statistics {
    gap: 0.6rem;
  }
}
@media all and (max-width: 499px) {
  .profile-wrapper .profile .profile__actions {
    gap: 0.4rem;
  }
}
.profile-wrapper .profile-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.profile-wrapper .profile-head__text {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.profile-wrapper .profile-head__title {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2.2rem;
}
.profile-wrapper .profile-head__title .icon {
  flex: 0 0 auto;
  font-size: 2.4rem;
}
.profile-wrapper .profile-head__subtitle {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: #cddbff;
}
.profile-wrapper .profile-head__subtitle .link {
  color: #fff;
  border-bottom: 1px solid #fff;
  transition: border-color 0.2s ease-out 0s;
}
.profile-wrapper .profile-head__subtitle .link:hover {
  border-color: transparent;
}
.profile-wrapper .profile-head__button {
  font-family: Gotham;
  border: 0;
  background-color: #62d4b2;
  color: #20274b;
  text-transform: none;
  padding: 1.2rem 2rem;
  height: auto;
  line-height: 1;
  transition: filter 0.2s ease-out 0s;
}
.profile-wrapper .profile-head__button:hover {
  filter: brightness(1.2);
}
.profile-wrapper .profile-head__button .button__text {
  margin-left: 0.5rem;
}
.profile-wrapper .profile-head__button .button__icon {
  font-size: 1.8rem;
}
@media all and (max-width: 767px) {
  .profile-wrapper .profile-head__button {
    flex: 0 0 auto;
    padding: 1.2rem;
  }
  .profile-wrapper .profile-head__button .button__text {
    display: none;
    margin: 0;
  }
  .profile-wrapper .profile-head__button .button__icon {
    margin: 0;
