    <users-profile-tabs _nghost-serverapp-c172="" class="ng-star-inserted" v-show="mobileTabs"
    ><div _ngcontent-serverapp-c172="" class="profile__tabs">
      
      <div
        _ngcontent-serverapp-c172=""
        maxwidth="100%"
        placement="bottom-start"
        variation="profile"
        class="profile__dropdown dropdown ng-star-inserted"
        aria-expanded="false"
        ref="ShowTabBtn"
      >
        
        <img
            src="../../public/images/icon/free-icon-account.png"
            class="icon" v-if="profile"
          />
        <img src="../../public/images/icon/retro-game.png" class="icon" v-if="games" />
        <img
            src="../../public/images/icon/wallet-passes-app.png"
            class="icon"
            v-if="deposits"
          />
        <img
            src="../../public/images/icon/thumb_Mask.png"
            style="transform: rotate(270deg)"
            class="icon"
            v-if="withdrawals"
          />  
        <div
          _ngcontent-serverapp-c172=""
          class="dropdown__title ng-star-inserted"
          v-if="profile"
        >
          Profile
        </div>  
        <div
          _ngcontent-serverapp-c172=""
          class="dropdown__title ng-star-inserted"
          v-if="games"
        >
          Games
        </div>
        <div
          _ngcontent-serverapp-c172=""
          class="dropdown__title ng-star-inserted"
          v-if="deposits"
        >
          Deposits
        </div>
        <div
          _ngcontent-serverapp-c172=""
          class="dropdown__title ng-star-inserted"
          v-if="withdrawals"
        >
          Withdrawals
        </div>
        <div
          _ngcontent-serverapp-c172=""
          class="dropdown__toggler ng-star-inserted"
        >
        <img src="../../public/images/icon/more.png" class="icon" />
        </div>
        
      </div>
      <button
        _ngcontent-serverapp-c172=""
        type="button"
        class="profile__logout button"
      >
        <span _ngcontent-serverapp-c172="" class="button__text">Exit</span
        >
        <img src="../../public/images/icon/logout.png" class="icon" />
      </button></div
  >
      <users-profile-tabs ref="DataTab">
        <ProfileTabsBtn/>
      </users-profile-tabs>
  </users-profile-tabs>
  <users-profile-tabs _nghost-serverapp-c172="" class="ng-star-inserted" v-show="!mobileTabs"
    ><div _ngcontent-serverapp-c172="" class="profile__tabs">
      <ProfileTabsBtn/>
      <button
        _ngcontent-serverapp-c172=""
        type="button"
        class="profile__logout button"
      >
        <span _ngcontent-serverapp-c172="" class="button__text">Exit</span>
        <img src="../../public/images/icon/logout.png" class="icon" />
      </button></div
  ></users-profile-tabs>

</template>

<script>
import ProfileTabsBtn from "./ProfileTabsBtn.vue"
import tippy from 'tippy.js'

export default {
  props: {
    profile: {
      type: Boolean,
      default: false,
    },
    games: {
      type: Boolean,
      default: false,
    },
    deposits: {
      type: Boolean,
      default: false,
    },
    withdrawals: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    ProfileTabsBtn
  },
  data() {
    return {
      mobileTabs: false,
    };
  },
  mounted() {
    if (window.innerWidth <= 991) {
      this.mobileTabs = true;
      tippy(this.$refs.ShowTabBtn, {
        arrow: false,
        maxWidth: 'none',
        theme: 'profile',
        content: this.$refs.DataTab,
        trigger: "click",
        interactive: true,
        animation: "shift-away",
      });
    }
  },
};
</script>

