<template>
  <payment-page-layout class="payment-page">
    <PaymentsTab money />
    <div class="payment-page__content">
      <router-outlet></router-outlet
      ><payment-tab-methods class="payment-tab-methods">
        <PaymentBonus />
        <PaymentBaner money />
        <payment-content-wrap
          contenttitle="Choose method"
          class="tab__paragraph"
          _nghost-serverapp-c163=""
          ><div _ngcontent-serverapp-c163="" class="payment-content__head">
            <div _ngcontent-serverapp-c163="" class="payment-content__heading">
              <div _ngcontent-serverapp-c163="" class="payment-content__title">
                Choose method
              </div>
            </div>
          </div>
          <div class="payment-methods">
            <payment-methods-option class="method-option" v-for="(card, index) in activeCards.cards" @click="ChangeActiveCard(index)">
              <span class="method-option__badge"  v-if="card.method != false"
                ><span class="method-option__badge_blur"></span
                ><span class="method-option__badge-text">
                  {{card.method}} methods
                </span></span>
              <span class="method-option__badge" v-if="card.animation != false"
                ><span class="method-option__badge_blur"></span
                ><span class="method-option__badge-text"> #{{card.animation}} </span></span
              >
              <animated-card class="animated-card method-option__animated-card" v-if="card.animation != false"
                ><div class="card card_mc"></div>
                <div
                  class="card card_visa"
                ></div> 
                <div v-if="card.animation == '3'"
                  class="card card_mir"
                ></div> 
                </animated-card>
              <img
                v-else
                alt="Payment option"
                class="method-option__img"
                :src="getPhoto(card.img)" />
            </payment-methods-option>
            
          </div> </payment-content-wrap
        ><payment-content-wrap class="tab__paragraph" _nghost-serverapp-c163=""
          ><div _ngcontent-serverapp-c163="" class="payment-content__head">
            <div _ngcontent-serverapp-c163="" class="payment-content__heading">
              <div
                _ngcontent-serverapp-c163=""
                class="payment-content__title"
              ></div>
            </div></div></payment-content-wrap
      ></payment-tab-methods>
    </div>
  </payment-page-layout>
</template>

<script>
import PaymentBonus from "../../components/PaymentBonus.vue";
import PaymentBaner from "../../components/PaymentBaner.vue";
import PaymentsTab from "../../components/PaymentsTab.vue";

export default {
  components: {
    PaymentBonus,
    PaymentBaner,
    PaymentsTab,
  },
  data() {
    return {
      activeCards: {
        activeCard:{ activate: false, method: false, name: 'Card', sign: 'EUR', img: false, animation: '1', email: false, alert: false, min: 100 },
        cards:[{ activate: false, method: '50+', name: 'VISA / MC', sign: 'EUR', img: '35b4ab81-7751-422e-a166-d3c6aec18fff-63f4a3c8a3037.svg', animation: false, email: true, alert: 'Will not work in Belgium, China, Japan, The Netherlands and the Isle of Man, Taiwan, Thailand, Switzerland, Norway, Denmark' },
        { activate: false, method: false, name: 'Card', sign: 'EUR', img: false, animation: '1', email: false, alert: false, min: 100 },
        { activate: false, method: false, name: 'VISA / MC', sign: 'EUR', img: false, animation: '2', email: false, alert: false, min: 100 },
        { activate: false, method: false, name: 'Card', sign: 'RUB', img: false, animation: '3', email: false, alert: false, min: 100 },
        { activate: false, method: false, name: 'Apple pay', sign: 'EUR', img: '1c3a0abf-59fc-436d-92a2-c548a6be9094-63a450f118342.svg', animation: false, email: true, alert: false, min: 100 },
        { activate: false, method: false, name: 'Google pay', sign: 'EUR', img: '249976cf-c512-4af9-b022-9cd73480d708-63a450f1183ce.svg', animation: false, email: true, alert: false, min: 100 },
        { activate: false, method: false, name: 'Advcash', sign: 'EUR', img: '0b8eec87-ca44-4284-b6b5-d8639d7df821-636d1ada44531.svg', animation: false, email: true, alert: false, min: 100 },
        { activate: false, method: false, name: 'Paydo', sign: 'EUR', img: '73ac607f-b2c9-43ce-a346-d67c7cdaebec-61e852748909c.svg', animation: false, email: true, alert: false, min: 100 },
        { activate: false, method: false, name: 'Neosurf', sign: 'EUR', img: '084ebd86-1a7d-47f6-9fd6-ad044bf01f95-61e8533a145e7.svg', animation: false, email: true, alert: false, min: 100 }]
    },
    };
  },
  methods:{
    getPhoto(img){
      return require('../../../public/images/'+img)
    },
    ChangeActiveCard(id){
      for(var card in this.activeCards.cards){
        if(card == id){
          this.activeCards.cards[id].activate=true
          this.activeCards.activeCard = this.activeCards.cards[id]
        }else{
          this.activeCards.cards[card].activate=false
        }
      }
      
      this.$emit('ShowPaytabs', this.activeCards)
      this.$store.state.modal = true
      this.$store.state.pay_card = true
    }
  }
};
</script>

